"use client";

import React, { useEffect, useMemo, useRef, useState } from "react";
import {
  createEmptyAddress,
  createEmptyEmployment,
  loadIntake,
  saveIntake,
  type AddressEntry,
  type EmploymentEntry,
  type IntakeData,
  type MonthValue,
} from "@/app/lib/intakeStorage";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { cn } from "@/lib/utils";
import { StepShell } from "./StepShell";

const MONTHS: Array<{ value: MonthValue; label: string }> = [
  { value: "01", label: "Jan" },
  { value: "02", label: "Feb" },
  { value: "03", label: "Mar" },
  { value: "04", label: "Apr" },
  { value: "05", label: "May" },
  { value: "06", label: "Jun" },
  { value: "07", label: "Jul" },
  { value: "08", label: "Aug" },
  { value: "09", label: "Sep" },
  { value: "10", label: "Oct" },
  { value: "11", label: "Nov" },
  { value: "12", label: "Dec" },
];

const getYearOptions = () => {
  const current = new Date().getFullYear();
  const years: string[] = [];
  for (let i = 0; i < 12; i += 1) {
    years.push(String(current - i));
  }
  return years;
};

const STEP_LABELS = [
  "Basics",
  "Contact",
  "Address history",
  "Employment history",
  "Review",
];

export function IntakeFlow() {
  const [stepIndex, setStepIndex] = useState(0);
  const [data, setData] = useState<IntakeData>(() => loadIntake());
  const [saveStatus, setSaveStatus] = useState<"idle" | "saved">("idle");
  const [isGenerating, setIsGenerating] = useState(false);
  const [pdfUrl, setPdfUrl] = useState<string | null>(null);
  const saveTimer = useRef<ReturnType<typeof setTimeout> | null>(null);

  useEffect(() => {
    if (saveTimer.current) {
      globalThis.clearTimeout(saveTimer.current);
    }
    saveTimer.current = globalThis.setTimeout(() => {
      saveIntake(data);
      setSaveStatus("saved");
    }, 400);

    return () => {
      if (saveTimer.current) {
        globalThis.clearTimeout(saveTimer.current);
      }
    };
  }, [data]);

  const yearOptions = useMemo(() => getYearOptions(), []);

  useEffect(() => {
    return () => {
      if (pdfUrl) {
        globalThis.URL.revokeObjectURL(pdfUrl);
      }
    };
  }, [pdfUrl]);

  const renderSelect = (
    value: string,
    onChange: (value: string) => void,
    placeholder: string,
    options: Array<{ value: string; label: string }>,
    disabled = false,
    className?: string
  ) => (
    <Select value={value || undefined} onValueChange={onChange} disabled={disabled}>
      <SelectTrigger className={cn("w-full", className)}>
        <SelectValue placeholder={placeholder} />
      </SelectTrigger>
      <SelectContent>
        {options.map((option) => (
          <SelectItem key={option.value} value={option.value}>
            {option.label}
          </SelectItem>
        ))}
      </SelectContent>
    </Select>
  );

  const goNext = () => {
    setStepIndex((prev) => Math.min(prev + 1, STEP_LABELS.length - 1));
  };
  const formatDob = () => {
    const { month, day, year } = data.basics.dateOfBirth;
    if (!month || !day || !year) {
      return "";
    }
    return `${month}/${day.padStart(2, "0")}/${year}`;
  };

  const formatMonthYear = (month: MonthValue, year: string) => {
    if (!month || !year) {
      return "";
    }
    return `${month}/${year}`;
  };

  const formatTimeline = (entry: {
    fromMonth: MonthValue;
    fromYear: string;
    toMonth: MonthValue;
    toYear: string;
    isCurrent: boolean;
  }) => {
    const start = formatMonthYear(entry.fromMonth, entry.fromYear);
    const end = entry.isCurrent
      ? "Present"
      : formatMonthYear(entry.toMonth, entry.toYear);
    if (!start && !end) {
      return "";
    }
    return `${start || "Unknown"} to ${end || "Unknown"}`;
  };

  const buildAdditionalInfo = () => {
    const MAX_ADDRESS_FIELDS = 2;
    const MAX_EMPLOYMENT_FIELDS = 2;
    const overflowAddresses = data.addresses.slice(MAX_ADDRESS_FIELDS);
    const overflowEmployment = data.employment.slice(MAX_EMPLOYMENT_FIELDS);
    const lines: string[] = [];

    overflowAddresses.forEach((entry, index) => {
      const address = [
        entry.street,
        entry.unit,
        entry.city,
        entry.state,
        entry.postal,
        entry.country,
      ]
        .filter(Boolean)
        .join(", ");
      const timeline = formatTimeline(entry);
      lines.push(
        `Address history (continued) ${index + 1}: ${address || "Address missing"} (${timeline || "Dates missing"})`
      );
    });

    overflowEmployment.forEach((entry, index) => {
      const employer = [
        entry.employerName,
        entry.jobTitle && `(${entry.jobTitle})`,
        [entry.city, entry.state, entry.country].filter(Boolean).join(", "),
      ]
        .filter(Boolean)
        .join(" ");
      const timeline = formatTimeline(entry);
      lines.push(
        `Employment history (continued) ${index + 1}: ${employer || "Employer missing"} (${timeline || "Dates missing"})`
      );
    });

    return lines.join("\n");
  };

  const buildPdfPayload = () => {
    const relationshipMap: Record<string, string> = {
      spouse: "form1[0].#subform[0].Pt1Line1_Spouse[0]",
      parent: "form1[0].#subform[0].Pt1Line1_Parent[0]",
      child: "form1[0].#subform[0].Pt1Line1_Child[0]",
      sibling: "form1[0].#subform[0].Pt1Line1_Siblings[0]",
    };

    const currentAddress = data.addresses[0];

    const additionalInfo = buildAdditionalInfo();

    return {
      fields: {
        "form1[0].#subform[0].Pt2Line4a_FamilyName[0]":
          data.basics.petitioner.familyName,
        "form1[0].#subform[0].Pt2Line4b_GivenName[0]":
          data.basics.petitioner.givenName,
        "form1[0].#subform[0].Pt2Line4c_MiddleName[0]":
          data.basics.petitioner.middleName,
        "form1[0].#subform[1].Pt2Line8_DateofBirth[0]": formatDob(),
        "form1[0].#subform[1].Pt2Line10_StreetNumberName[0]":
          currentAddress?.street || "",
        "form1[0].#subform[1].Pt2Line10_AptSteFlrNumber[0]":
          currentAddress?.unit || "",
        "form1[0].#subform[1].Pt2Line10_CityOrTown[0]":
          currentAddress?.city || "",
        "form1[0].#subform[1].Pt2Line10_State[0]":
          currentAddress?.state || "",
        "form1[0].#subform[1].Pt2Line10_ZipCode[0]":
          currentAddress?.postal || "",
        "form1[0].#subform[1].Pt2Line10_Country[0]":
          currentAddress?.country || "",
        "form1[0].#subform[11].Pt9Line7d_AdditionalInfo[0]": additionalInfo,
      },
      checkboxes: {
        [relationshipMap[data.basics.relationship] ?? ""]: true,
      },
    };
  };

  const handleSave = () => {
    saveIntake(data);
    setSaveStatus("saved");
  };

  const handleGenerate = async () => {
    setIsGenerating(true);
    try {
      const apiBase =
        process.env.NEXT_PUBLIC_API_URL || "http://localhost:8000";
      const payload = buildPdfPayload();
      const response = await fetch(`${apiBase}/fill/i-130`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(payload),
      });
      if (!response.ok) {
        throw new Error(`Failed to generate PDF (${response.status})`);
      }
      const blob = await response.blob();
      const url = globalThis.URL.createObjectURL(blob);
      if (pdfUrl) {
        globalThis.URL.revokeObjectURL(pdfUrl);
      }
      setPdfUrl(url);
    } finally {
      setIsGenerating(false);
    }
  };
  const goBack = () => {
    setStepIndex((prev) => Math.max(prev - 1, 0));
  };

  const updateAddress = (id: string, patch: Partial<AddressEntry>) => {
    setData((prev) => ({
      ...prev,
      addresses: prev.addresses.map((entry) =>
        entry.id === id ? { ...entry, ...patch } : entry
      ),
    }));
  };

  const updateEmployment = (id: string, patch: Partial<EmploymentEntry>) => {
    setData((prev) => ({
      ...prev,
      employment: prev.employment.map((entry) =>
        entry.id === id ? { ...entry, ...patch } : entry
      ),
    }));
  };

  const addAddress = () =>
    setData((prev) => ({
      ...prev,
      addresses: [...prev.addresses, createEmptyAddress()],
    }));

  const addEmployment = () =>
    setData((prev) => ({
      ...prev,
      employment: [...prev.employment, createEmptyEmployment()],
    }));

  const removeAddress = (id: string) =>
    setData((prev) => ({
      ...prev,
      addresses: prev.addresses.filter((entry) => entry.id !== id),
    }));

  const removeEmployment = (id: string) =>
    setData((prev) => ({
      ...prev,
      employment: prev.employment.filter((entry) => entry.id !== id),
    }));

  return (
    <div className="flex flex-col gap-6">
      <div className="flex flex-wrap items-center gap-3 text-sm text-zinc-500">
        {STEP_LABELS.map((label, idx) => (
          <div
            key={label}
            className={`rounded-full px-3 py-1 ${
              idx === stepIndex
                ? "bg-zinc-900 text-white dark:bg-zinc-100 dark:text-zinc-900"
                : "bg-zinc-100 text-zinc-600 dark:bg-zinc-800 dark:text-zinc-300"
            }`}
          >
            {label}
          </div>
        ))}
      </div>

      {stepIndex === 0 ? (
        <StepShell
          title="Let’s start with the basics"
          description="We’ll keep this short and friendly. You can edit anything later."
        >
          <div className="grid gap-4 sm:grid-cols-2">
            <label className="flex flex-col gap-2 text-sm">
              <Label>Relationship to beneficiary</Label>
              {renderSelect(
                data.basics.relationship,
                (value) =>
                  setData((prev) => ({
                    ...prev,
                    basics: {
                      ...prev.basics,
                      relationship: value as IntakeData["basics"]["relationship"],
                    },
                  })),
                "Select",
                [
                  { value: "spouse", label: "Spouse" },
                  { value: "parent", label: "Parent" },
                  { value: "child", label: "Child" },
                  { value: "sibling", label: "Brother/Sister" },
                ]
              )}
            </label>
            <div className="rounded-lg border border-dashed border-zinc-300 p-3 text-xs text-zinc-500 dark:border-zinc-700">
              We’ll use this to shape which I-130 questions you see.
            </div>
          </div>

          <div className="grid gap-4 sm:grid-cols-3">
            <label className="flex flex-col gap-2 text-sm">
              <Label>Given name</Label>
              <Input
                value={data.basics.petitioner.givenName}
                onChange={(event) =>
                  setData((prev) => ({
                    ...prev,
                    basics: {
                      ...prev.basics,
                      petitioner: {
                        ...prev.basics.petitioner,
                        givenName: event.target.value,
                      },
                    },
                  }))
                }
                placeholder="Alex"
              />
            </label>
            <label className="flex flex-col gap-2 text-sm">
              <Label>Middle name</Label>
              <Input
                value={data.basics.petitioner.middleName}
                onChange={(event) =>
                  setData((prev) => ({
                    ...prev,
                    basics: {
                      ...prev.basics,
                      petitioner: {
                        ...prev.basics.petitioner,
                        middleName: event.target.value,
                      },
                    },
                  }))
                }
                placeholder="Leave blank if none"
              />
            </label>
            <label className="flex flex-col gap-2 text-sm">
              <Label>Family name</Label>
              <Input
                value={data.basics.petitioner.familyName}
                onChange={(event) =>
                  setData((prev) => ({
                    ...prev,
                    basics: {
                      ...prev.basics,
                      petitioner: {
                        ...prev.basics.petitioner,
                        familyName: event.target.value,
                      },
                    },
                  }))
                }
                placeholder="Doe"
              />
            </label>
          </div>

          <div className="grid gap-4 sm:grid-cols-3">
            <label className="flex flex-col gap-2 text-sm">
              <Label>Birth month</Label>
              {renderSelect(
                data.basics.dateOfBirth.month,
                (value) =>
                  setData((prev) => ({
                    ...prev,
                    basics: {
                      ...prev.basics,
                      dateOfBirth: {
                        ...prev.basics.dateOfBirth,
                        month: value as MonthValue,
                      },
                    },
                  })),
                "Month",
                MONTHS.map((month) => ({
                  value: month.value,
                  label: month.label,
                }))
              )}
            </label>
            <label className="flex flex-col gap-2 text-sm">
              <Label>Birth day</Label>
              <Input
                inputMode="numeric"
                value={data.basics.dateOfBirth.day}
                onChange={(event) =>
                  setData((prev) => ({
                    ...prev,
                    basics: {
                      ...prev.basics,
                      dateOfBirth: {
                        ...prev.basics.dateOfBirth,
                        day: event.target.value,
                      },
                    },
                  }))
                }
                placeholder="DD"
              />
            </label>
            <label className="flex flex-col gap-2 text-sm">
              <Label>Birth year</Label>
              <Input
                inputMode="numeric"
                value={data.basics.dateOfBirth.year}
                onChange={(event) =>
                  setData((prev) => ({
                    ...prev,
                    basics: {
                      ...prev.basics,
                      dateOfBirth: {
                        ...prev.basics.dateOfBirth,
                        year: event.target.value,
                      },
                    },
                  }))
                }
                placeholder="YYYY"
              />
            </label>
          </div>

        </StepShell>
      ) : null}

      {stepIndex === 1 ? (
        <StepShell
          title="How can we reach you?"
          description="We only ask for what’s needed for USCIS forms."
        >
          <div className="grid gap-4 sm:grid-cols-2">
            <label className="flex flex-col gap-2 text-sm">
              <Label>Email address</Label>
              <Input
                type="email"
                value={data.contact.email}
                onChange={(event) =>
                  setData((prev) => ({
                    ...prev,
                    contact: { ...prev.contact, email: event.target.value },
                  }))
                }
                placeholder="you@email.com"
              />
            </label>
            <label className="flex flex-col gap-2 text-sm">
              <Label>Phone number</Label>
              <Input
                value={data.contact.phone}
                onChange={(event) =>
                  setData((prev) => ({
                    ...prev,
                    contact: { ...prev.contact, phone: event.target.value },
                  }))
                }
                placeholder="(555) 123-4567"
              />
            </label>
          </div>
        </StepShell>
      ) : null}

      {stepIndex === 2 ? (
        <StepShell
          title="Address history (last 5 years)"
          description="Start with your current address and work backwards. It’s okay to estimate months."
        >
          {data.addresses.map((entry, idx) => (
            <div
              key={entry.id}
              className="rounded-xl border border-zinc-200 bg-zinc-50 p-4 dark:border-zinc-800 dark:bg-zinc-900"
            >
              <div className="flex items-center justify-between">
                <p className="text-sm font-semibold text-zinc-900 dark:text-zinc-100">
                  {idx === 0 ? "Current address" : `Previous address ${idx}`}
                </p>
                {data.addresses.length > 1 ? (
                  <Button
                    type="button"
                    variant="ghost"
                    size="sm"
                    onClick={() => removeAddress(entry.id)}
                  >
                    Remove
                  </Button>
                ) : null}
              </div>

              <div className="mt-4 grid gap-4 sm:grid-cols-2">
                <label className="flex flex-col gap-2 text-sm sm:col-span-2">
                  <Label>Street address</Label>
                  <Input
                    value={entry.street}
                    onChange={(event) =>
                      updateAddress(entry.id, { street: event.target.value })
                    }
                    placeholder="123 Main St"
                  />
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>Unit / Apt</Label>
                  <Input
                    value={entry.unit}
                    onChange={(event) =>
                      updateAddress(entry.id, { unit: event.target.value })
                    }
                    placeholder="Apt 2B"
                  />
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>City</Label>
                  <Input
                    value={entry.city}
                    onChange={(event) =>
                      updateAddress(entry.id, { city: event.target.value })
                    }
                    placeholder="San Jose"
                  />
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>State</Label>
                  <Input
                    value={entry.state}
                    onChange={(event) =>
                      updateAddress(entry.id, { state: event.target.value })
                    }
                    placeholder="CA"
                  />
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>ZIP / Postal code</Label>
                  <Input
                    value={entry.postal}
                    onChange={(event) =>
                      updateAddress(entry.id, { postal: event.target.value })
                    }
                    placeholder="95112"
                  />
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>Country</Label>
                  <Input
                    value={entry.country}
                    onChange={(event) =>
                      updateAddress(entry.id, { country: event.target.value })
                    }
                    placeholder="United States"
                  />
                </label>
              </div>

              <div className="mt-4 grid gap-4 sm:grid-cols-4">
                <label className="flex flex-col gap-2 text-sm">
                  <Label>From month</Label>
                  {renderSelect(
                    entry.fromMonth,
                    (value) =>
                      updateAddress(entry.id, { fromMonth: value as MonthValue }),
                    "Month",
                    MONTHS.map((month) => ({
                      value: month.value,
                      label: month.label,
                    }))
                  )}
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>From year</Label>
                  {renderSelect(
                    entry.fromYear,
                    (value) => updateAddress(entry.id, { fromYear: value }),
                    "Year",
                    yearOptions.map((year) => ({ value: year, label: year }))
                  )}
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>To month</Label>
                  {renderSelect(
                    entry.toMonth,
                    (value) =>
                      updateAddress(entry.id, { toMonth: value as MonthValue }),
                    "Month",
                    MONTHS.map((month) => ({
                      value: month.value,
                      label: month.label,
                    })),
                    entry.isCurrent
                  )}
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>To year</Label>
                  {renderSelect(
                    entry.toYear,
                    (value) => updateAddress(entry.id, { toYear: value }),
                    "Year",
                    yearOptions.map((year) => ({ value: year, label: year })),
                    entry.isCurrent
                  )}
                </label>
              </div>

              <div className="mt-4 flex items-center justify-between">
                <label className="flex items-center gap-2 text-sm">
                  <input
                    type="checkbox"
                    checked={entry.isCurrent}
                    onChange={(event) =>
                      updateAddress(entry.id, {
                        isCurrent: event.target.checked,
                        toMonth: event.target.checked ? "" : entry.toMonth,
                        toYear: event.target.checked ? "" : entry.toYear,
                      })
                    }
                    className="h-4 w-4 rounded border-zinc-300 text-zinc-900"
                  />
                  <span>Current address</span>
                </label>
              </div>
            </div>
          ))}

          <Button type="button" variant="secondary" onClick={addAddress}>
            Add another address
          </Button>
        </StepShell>
      ) : null}

      {stepIndex === 3 ? (
        <StepShell
          title="Employment history (last 5 years)"
          description="Start with your current situation and work backwards."
        >
          {data.employment.map((entry, idx) => (
            <div
              key={entry.id}
              className="rounded-xl border border-zinc-200 bg-zinc-50 p-4 dark:border-zinc-800 dark:bg-zinc-900"
            >
              <div className="flex items-center justify-between">
                <p className="text-sm font-semibold text-zinc-900 dark:text-zinc-100">
                  {idx === 0 ? "Current employment" : `Previous employment ${idx}`}
                </p>
                {data.employment.length > 1 ? (
                  <Button
                    type="button"
                    variant="ghost"
                    size="sm"
                    onClick={() => removeEmployment(entry.id)}
                  >
                    Remove
                  </Button>
                ) : null}
              </div>

              <div className="mt-4 grid gap-4 sm:grid-cols-2">
                <label className="flex flex-col gap-2 text-sm">
                  <Label>Status</Label>
                  {renderSelect(
                    entry.status,
                    (value) =>
                      updateEmployment(entry.id, {
                        status: value as EmploymentEntry["status"],
                      }),
                    "Select",
                    [
                      { value: "employed", label: "Employed" },
                      { value: "unemployed", label: "Unemployed" },
                      { value: "student", label: "Student" },
                      { value: "other", label: "Other" },
                    ]
                  )}
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>Employer name</Label>
                  <Input
                    value={entry.employerName}
                    onChange={(event) =>
                      updateEmployment(entry.id, {
                        employerName: event.target.value,
                      })
                    }
                    placeholder="Company name"
                  />
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>Job title</Label>
                  <Input
                    value={entry.jobTitle}
                    onChange={(event) =>
                      updateEmployment(entry.id, { jobTitle: event.target.value })
                    }
                    placeholder="Software Engineer"
                  />
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>City</Label>
                  <Input
                    value={entry.city}
                    onChange={(event) =>
                      updateEmployment(entry.id, { city: event.target.value })
                    }
                    placeholder="San Jose"
                  />
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>State</Label>
                  <Input
                    value={entry.state}
                    onChange={(event) =>
                      updateEmployment(entry.id, { state: event.target.value })
                    }
                    placeholder="CA"
                  />
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>Country</Label>
                  <Input
                    value={entry.country}
                    onChange={(event) =>
                      updateEmployment(entry.id, { country: event.target.value })
                    }
                    placeholder="United States"
                  />
                </label>
              </div>

              <div className="mt-4 grid gap-4 sm:grid-cols-4">
                <label className="flex flex-col gap-2 text-sm">
                  <Label>From month</Label>
                  {renderSelect(
                    entry.fromMonth,
                    (value) =>
                      updateEmployment(entry.id, {
                        fromMonth: value as MonthValue,
                      }),
                    "Month",
                    MONTHS.map((month) => ({
                      value: month.value,
                      label: month.label,
                    }))
                  )}
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>From year</Label>
                  {renderSelect(
                    entry.fromYear,
                    (value) =>
                      updateEmployment(entry.id, { fromYear: value }),
                    "Year",
                    yearOptions.map((year) => ({ value: year, label: year }))
                  )}
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>To month</Label>
                  {renderSelect(
                    entry.toMonth,
                    (value) =>
                      updateEmployment(entry.id, { toMonth: value as MonthValue }),
                    "Month",
                    MONTHS.map((month) => ({
                      value: month.value,
                      label: month.label,
                    })),
                    entry.isCurrent
                  )}
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>To year</Label>
                  {renderSelect(
                    entry.toYear,
                    (value) => updateEmployment(entry.id, { toYear: value }),
                    "Year",
                    yearOptions.map((year) => ({ value: year, label: year })),
                    entry.isCurrent
                  )}
                </label>
              </div>

              <div className="mt-4 flex items-center justify-between">
                <label className="flex items-center gap-2 text-sm">
                  <input
                    type="checkbox"
                    checked={entry.isCurrent}
                    onChange={(event) =>
                      updateEmployment(entry.id, {
                        isCurrent: event.target.checked,
                        toMonth: event.target.checked ? "" : entry.toMonth,
                        toYear: event.target.checked ? "" : entry.toYear,
                      })
                    }
                    className="h-4 w-4 rounded border-zinc-300 text-zinc-900"
                  />
                  <span>Current situation</span>
                </label>
              </div>
            </div>
          ))}

          <Button type="button" variant="secondary" onClick={addEmployment}>
            Add another job or status
          </Button>
        </StepShell>
      ) : null}

      {stepIndex === 4 ? (
        <StepShell
          title="Quick review"
          description="We’ll highlight anything missing so you can fix it."
        >
          <div className="rounded-lg border border-zinc-200 bg-zinc-50 p-4 text-sm text-zinc-600 dark:border-zinc-800 dark:bg-zinc-900 dark:text-zinc-300">
            <p>
              Addresses entered: <strong>{data.addresses.length}</strong>
            </p>
            <p className="mt-1">
              Employment entries: <strong>{data.employment.length}</strong>
            </p>
            <p className="mt-2">
              We’ll flag timeline gaps and missing details once the mapping is
              connected to the PDF.
            </p>
          </div>
          <label className="flex flex-col gap-2 text-sm">
            <Label>Anything you want to revisit later?</Label>
            <Textarea
              value={data.addresses[0]?.notes || ""}
              onChange={(event) =>
                updateAddress(data.addresses[0].id, {
                  notes: event.target.value,
                })
              }
              placeholder="Optional notes for later..."
            />
          </label>

          <div className="flex flex-wrap items-center gap-3">
            <Button
              type="button"
              variant="secondary"
              onClick={handleSave}
              disabled={saveStatus === "saved"}
            >
              {saveStatus === "saved" ? "Saved" : "Save progress"}
            </Button>
            <Button type="button" onClick={handleGenerate} disabled={isGenerating}>
              {isGenerating ? "Generating..." : "Generate PDF preview"}
            </Button>
          </div>

          {pdfUrl ? (
            <div className="rounded-lg border border-zinc-200 bg-zinc-50 p-3 dark:border-zinc-800 dark:bg-zinc-900">
              <p className="text-sm text-zinc-600 dark:text-zinc-300">
                PDF preview (first pass mapping)
              </p>
              <iframe
                title="I-130 preview"
                src={pdfUrl}
                className="mt-3 h-[560px] w-full rounded-md border border-zinc-200 bg-white dark:border-zinc-800"
              />
            </div>
          ) : null}
        </StepShell>
      ) : null}

      <div className="flex items-center justify-between">
        <Button
          type="button"
          variant="ghost"
          onClick={goBack}
          disabled={stepIndex === 0}
        >
          Back
        </Button>
        <div className="flex items-center gap-2">
          {stepIndex < STEP_LABELS.length - 1 ? (
            <Button type="button" onClick={goNext}>
              Continue
            </Button>
          ) : (
            <Button type="button" variant="secondary" onClick={handleSave}>
              {saveStatus === "saved" ? "Saved" : "Save progress"}
            </Button>
          )}
        </div>
      </div>
    </div>
  );
}
